body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-loading {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.form-wrapper {
  padding: 1rem;
  border-bottom: 1px solid #22cf29;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Item{
    background-color:white;
    width:100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items:flex-start;
            align-items:flex-start;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
    margin-bottom:0px;
    text-overflow: ellipsis;
    padding:0 20px;
    box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133,0.22);
}

.Item .Item_Title{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
    -webkit-align-items:flex-start;
            align-items:flex-start;
    height: 45px;
}

.Item:hover {
    background: #bde7ee;
}

/* 마우스가 있을때만 .Item_Remove 보이기 */
.Item:hover .Item_Remove {
    opacity: 1;
}

.Item_Remove {
    color: #e64980;
    opacity: 0;
}

.Item_Remove:hover {
    cursor: pointer;
}

.Item_Column{
    width:30%;
    box-sizing:border-box;
    text-overflow: ellipsis;
}

.Item_Column:last-child{
    padding:10px 0;
    width:60%;
}

.Item h1{
    font-size:20px;
    text-align: left;
}

.Item .Item_Content{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
}

.Item .Item_Yesterday{
    font-size:7px;
    margin-top:7px;
}

.Item .Item_Price{
    margin-bottom:10px;
    margin-left:5px;
}

.Item .Item_DiffPercent{
    margin-bottom:10px;
    margin-left:5px;
}

@media screen and (min-width:320px) and (max-width:667px){
    .Item{
        width:100%;
    }
    .Item_Title{
        width:100%;
    }
}

.form {
  display: -webkit-flex;
  display: flex;
}

.form input {
  -webkit-flex: 1 1;
          flex: 1 1; /* 버튼을 뺀 빈 공간을 모두 채워줍니다 */
  font-size: 1rem;
  outline: none;
  height: 38px;
  border: 1px solid #143a14;
}

.create-button {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: 1rem;
  background: #143a14;
  border-radius: 3px;
  color: white;
  font-weight: 600;
  cursor: pointer;
}

.create-button:hover {
  background: #22cf29;
}
